body {
  font-family: 'Montserrat', sans-serif;
}
.wrapper {
  max-width:1200px;
  padding: 8px;
  margin: 0 auto;
  display: flex;
  flex-direction:column;
  justify-content:center;
  min-height:90vh;
  box-sizing: border-box;
}
p {
  text-align:center;
}
.wrapperCircle {
  min-height: 40px;
}


.sqCircle {
  min-height: 40px;
}
.h1 {
  font-size: 24px;
  font-weight: bold;
  margin:15px 0;
}
input {display:block;margin:10px auto;width:150px;}

.modal {
  display: none;
  position: fixed;
  width: 400px;
  right: 0;
  top: 0;
}

.modalLeft {
  display: none;
  position: fixed;
  width: 400px;
  left: 0;
  top: 0;
}

.flex-center {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: left;
}

.flex-center > div {
  width: 100%;
  padding-left: 30px;
}
